<template>
  <div>
    <form @submit="addTodo">
      <!-- v-model="title" used to save value of input:text to "title" -->
      <input
        type="text"
        v-model="title"
        name="title"
        placeholder="Add Todo..."
      />
      <input type="submit" value="Submit" class="btn" />
    </form>
  </div>
</template>

<script>
export default {
  name: "AddTodo",
  data() {
    return {
      title: "",
    };
  },
  methods: {
    addTodo(e) {
      // Prevent form default behavior (sending form)
      e.preventDefault();
      const newTodo = {
        title: this.title,
        completed: false,
      };
      // Emit add-todo to HomeView.vue
      this.$emit("add-todo", newTodo);
      // Clear input:text everytime new todo list added
      this.title = "";
    },
  },
};
</script>

<style scoped>
form {
  display: flex;
}

input[type="text"] {
  flex: 10;
  padding: 5px;
}

input[type="submit"] {
  flex: 2;
}
</style>